import Vue from 'vue';
import { Button, Search, Swipe, SwipeItem, Lazyload, Tabbar, TabbarItem, Field, Icon, RadioGroup, Radio, Cell, CellGroup, Checkbox, CheckboxGroup, Uploader, Stepper, Badge, BadgeGroup, TreeSelect, Popup, PasswordInput, NumberKeyboard, NavBar, Picker } from 'vant';
// 单元格
Vue.use(Cell).use(CellGroup);
// icon
Vue.use(Icon);
// 单选框
Vue.use(RadioGroup).use(Radio);
// 复选框
Vue.use(Checkbox).use(CheckboxGroup);
// 按钮
Vue.use(Button);
// 搜索
Vue.use(Search);
// 轮播图
Vue.use(Swipe).use(SwipeItem);
// 图片懒加载
Vue.use(Lazyload, {
  preLoad: 1.3,
  error: './image/address.png',
  loading: './image/address.png',
  attempt: 3
});
// 底部导航
Vue.use(Tabbar).use(TabbarItem);
// 输入框
Vue.use(Field);
// 步进器
Vue.use(Stepper);
// Badge 徽章
Vue.use(Badge).use(BadgeGroup);
// 上传
Vue.use(Uploader);
// 分类选择
Vue.use(TreeSelect)
// 弹框
Vue.use(Popup)
// 密码输入框
Vue.use(PasswordInput).use(NumberKeyboard);
// 导航栏
Vue.use(NavBar)
// 选择器
Vue.use(Picker)


