import Vue from 'vue';
import Router from 'vue-router';
Vue.use(Router);
export default new Router({
  mode: "history",
  routes: [
    {
      path: '/home',
      name: 'home',
      meta: {
        title: '首页'
      },
      //懒加载
      component: () => import('@/home/Home')
    },
    // 登录页面
    {
      path: '/',
      name: 'login',
      meta: {
        title: '登录'
      },
      //懒加载
      component: () => import('@/login/Login')
    },
    // 验证码登录
    {
      path: '/VerificationLogin',
      name: 'verificationLogin',
      meta: {
        title: '验证码登录'
      },
      //懒加载
      component: () => import('@/login/VerificationLogin')
    },
    // 注册页面
    { 
      path: '/Registered',
      name: 'registered',
      meta: { 
        title: '注册账号'
      },
      //懒加载
      component: () => import('@/login/Registered')
    },
    // 信息注册
    {
      path: '/InformationReg',
      name: 'informationReg',
      meta: {
        title: '信息注册'
      },
      //懒加载
      component: () => import('@/login/InformationReg')
    },
    // 找回密码
    {
      path: '/RetrievePassword',
      name: 'retrievePassword',
      meta: {
        title: '找回密码'
      },
      //懒加载
      component: () => import('@/login/RetrievePassword')
    },
    // 设置新密码
    {
      path: '/Newpassword',
      name: 'newpassword',
      meta: {
        title: '设置新密码'
      },
      //懒加载
      component: () => import('@/login/Newpassword')
    },
    // 分类 
    {
      path: '/Classification',
      name: 'classification',
      meta: {
        title: '信息注册'
      },
      //懒加载
      component: () => import('@/classification/Classification')
    },
    // 订单
    {
      path: '/Order',
      name: 'order',
      meta: {
        title: '订单'
      },
      //懒加载
      component: () => import('@/order/Order')
    },
    // 我的
    {
      path: '/My',
      name: 'my',
      meta: {
        title: '订单'
      },
      //懒加载
      component: () => import('@/my/My')
    },
  ]
});



